<template>
  <div class="home">
    <div class="claim-area">
        <Card :bordered="false">
            <p slot="title">Mdex 信息面板 <a href="#" @click="initData">刷新</a></p>
            <div>
                最新区块：{{mdex_panel.block_number}}
                <template>
                    <div class="gas_area">
                        <Row>
                            <Col span="8">快速 <span class="txt_gas">{{mdex_panel.defibox.fast.gas}}Gwei</span> ${{mdex_panel.defibox.fast.money}} / {{mdex_panel.defibox.fast.time}} 秒 </Col>
                            <Col span="8">标准 <span class="txt_gas">{{mdex_panel.defibox.standard.gas}}Gwei</span> ${{mdex_panel.defibox.standard.money}} / {{mdex_panel.defibox.standard.time}} 秒 </Col>
                            <Col span="8">慢 <span class="txt_gas">{{mdex_panel.defibox.slow.gas}}Gwei</span> ${{mdex_panel.defibox.slow.money}} / {{mdex_panel.defibox.slow.time}} 秒</Col>
                        </Row>
                    </div>
                </template>
            </div>
            <div>
                <Card :bordered="false">
                    <List border size="small">
                        <ListItem>
                            当前价格：<span class="txt-red">{{boardroom.curr_price}}</span> / 
                            回购价格：<span class="txt-red">{{boardroom.repurch_price}}</span>
                        </ListItem>
                        <ListItem>
                            回购合约余额：<span class="txt-red">$ {{boardroom.repurch_usdt}}</span>
                        </ListItem>
                        <ListItem>
                            最新流动性质押：<span class="txt-red">$ {{this.useNumberFilter(mdex_panel['charts']['mdex_tvl'], 4)}}</span>
                        </ListItem>
                         <ListItem>
                            24H交易额：<span class="txt-red">$ {{this.useNumberFilter(mdex_panel['charts']['volume_24hr'], 4)}}</span>
                        </ListItem>
                         <ListItem>
                            累计交易总额：<span class="txt-red">$ {{this.useNumberFilter(mdex_panel['charts']['total_volume'], 4)}}</span>
                        </ListItem>
                    </List>
                </Card>
            </div>
            <Tabs type="card">
                <TabPane label="回购地址">
                    <div class="area-input">
                        <template>
                            <Table :columns="repurch_cloumns" :data="repurch_data"></Table>
                        </template>
                    </div>
                </TabPane>
                <TabPane label="预言机地址">
                    <div class="area-input">
                        <template>
                            <Table :columns="oracle_cloumns" :data="oracle_data" max-height="450"></Table>
                        </template>
                    </div>
                </TabPane>
                <TabPane label="回购记录">
                    <div class="area-input">
                        <template>
                            <Table :columns="repurch_list_cloumns" :data="repurch_list" max-height="450"></Table>
                        </template>
                    </div>
                </TabPane>
            </Tabs>
            <Button size="large" type="primary" @click="queryBalances">查询余额</Button>
        </Card>
    </div>
  </div>
</template>

<script>
const Web3 = require("web3");
const HecoWeb3  =   new Web3('https://http-mainnet-node.huobichain.com');
const { cutString, timestampToTime, numberFilter } = require('../utils/tools');
const { erc20Contract, oracleContract, destoryOracleContract } = require("../utils/contracts/mdex_contract");

export default {
    name: 'Home',
    data () {
        return {
            timer: '',
            mdex_panel : {
                block_number: 0,
                charts: {},
                defibox: {
                    "fast": {
                        "gas": 0,
                        "money": "0",
                        "time": 0
                    },
                    "standard": {
                        "gas": 0,
                        "money": "0",
                        "time": 0
                    },
                    "slow": {
                        "gas": 0,
                        "money": "0",
                        "time": 0
                    }
                }
            },
            boardroom : {
                repurch_price: 0,
                curr_price: 0,
                repurch_usdt: 0
            },
            repurch_cloumns: [
                {
                    title: '地址',
                    key: 'address'
                },
                {
                    title: '余额',
                    sortable : true,
                    key: 'balance'
                },
                {
                    title: '类型',
                    key: 'addr_type'
                },
                {
                    title: '操作',
                    key: 'action',
                    fixed: 'right',
                    width: 120,
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'text',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.showRepurch(params.index)
                                    }
                                }
                            }, '查看')
                        ]);
                    }
                }
            ],
            oracle_cloumns: [
                {
                    title: '地址',
                    key: 'address'
                },
                {
                    title: '余额',
                    sortable : true,
                    key: 'balance'
                },
                {
                    title: '类型',
                    key: 'addr_type'
                },
                {
                    title: '操作',
                    key: 'action',
                    fixed: 'right',
                    width: 120,
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'text',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.showOracle(params.index)
                                    }
                                }
                            }, '查看')
                        ]);
                    }
                }
            ],
            repurch_list_cloumns: [
                {
                    title: '区块',
                    sortable : true,
                    key: 'block_number'
                },
                {
                    title: '交易哈希',
                    key: 'hash',
                    render: (h, params) => {
                        return h('div', [
                            h('A', {
                                on: {
                                    click: () => {
                                        this.repurchInfo(params.index)
                                    }
                                }
                            }, cutString(params.row.hash))
                        ]);
                    }
                },
                {
                    title: 'From',
                    key: 'from',
                    render: (h, params) => {
                        return h('div', [
                            h('A', {
                                on: {
                                    click: () => {
                                        this.repurchInfo(params.index, 'from')
                                    }
                                }
                            }, cutString(params.row.from))
                        ]);
                    }
                },
                {
                    title: 'To',
                    key: 'to',
                    render: (h, params) => {
                        return h('div', [
                            h('A', {
                                on: {
                                    click: () => {
                                        this.repurchInfo(params.index, 'to')
                                    }
                                }
                            }, cutString(params.row.to))
                        ]);
                    }
                },
                {
                    title: '回购时间',
                    key: 'repurch_time'
                },
                {
                    title: '操作',
                    key: 'action',
                    fixed: 'right',
                    width: 120,
                    render: (h, params) => {
                        return h('div', [
                            h('Button', {
                                props: {
                                    type: 'text',
                                    size: 'small'
                                },
                                on: {
                                    click: () => {
                                        this.repurchInfo(params.index)
                                    }
                                }
                            }, '查看')
                        ]);
                    }
                }
            ],
            repurch_data: [],
            oracle_data:  [],
            repurch_list: [],
            accounts: {
                "repurch"   :   [
                    "0x7Fa4F6748D61d2EC782370F28Ab5Ad92Af9092f6",
                    "0x0B1ed35355d059b7065d12A081b63a1F12AcEEaF",
                    "0x4757302095300de5d67B476d57D61d78E091e2Ec",
                    "0x02E8929d607423C71968F07A70c27fe085959478",
                    "0x20B6FEF45fB9837A15464B6c81CC8974e64cE724",
                    "0x639cb11EB7807152DF9c213dBdC487045d13eA65",
                    "0x4F34090C9D68513D32141fE993Cd6399313A7713",
                    "0x8F306c0820F4375b3ae6F34f6CD156198371B8e3",
                    "0xad1837fDCe329EBe92669F8682401ceD393a9Eaa"
                ],

                "oracle"    :   [
                    "0xa6420fe4f01A53669C7285A536464564fc157034",
                    "0x11734Fade213D400683F5fd5f0712867Bd4d13ae",
                    "0xCE82F3cC51bf9d70e5A461277Ffa5Af22f392e66",
                    "0x079e4a7A00599a4d6B8c0bD1D1Dd6CA852504E3E",
                    "0x8b239FEAdE24A72056A241A812ad103975d1BE9e",
                    "0xBd6793f2f08391BC66b4eF2164867771215ad860",
                    "0xd157caB1E8619e93a029ba58215A5E7454C15Aa6",
                    "0x666BEbC1c99863ae4635dEAcc87CE83a873A14Eb",
                    "0x2DAC835E52536A1cbA6195980faB9a5CA46aC0c5",
                    "0x2Ac6e053FE8742CB77C29a8731A31256b410c77F",
                    "0x513717EcBc3A652641bC2Bb118a51dD7ac979FBa",
                    "0x0F2b04e156dad9323cA883f4b6E54C33F17440DE",
                    "0x3e24b58765f6a7C3f31b77c6F9361873AA6a1810",
                    "0x4D580c6e887c9E3941a0583f398720Ef4B1Ab384",
                    "0x1B80eB372141e129fc269778E034F51f6aeA51da",
                    "0x374bfEd1051d19b664f2B77ffFdd662cf56d3DDb",
                    "0x5A6a7Dd5B8e4Cc166bA973e3f257992D57CCd562",
                    "0x8928FE09BcE9bDc04C8Dd0C49923d9Db901925dd",
                    "0x7321033AC5E4983B8949dC1A537B03f5DC403031",
                    "0x6018B754288c21A4E421f9761F6e160deD42fd79",
                    "0xB31fCfFA6e61D4d5610Cc3c25440F7d0Df407732"
                ]
            }
        }
    },
    mounted: function () {
        this.initData();

        this.timer = setInterval(this.initData, 5000);
    },
    beforeDestroy() {
      clearTimeout(this.timer);
    },
    methods : {
        async initData() {
            this.queryBalances();
            this.queryPrice();
            this.panelData();
            this.queryRepurchList();
            this.queryMdexCharts();
            this.queryGasPrice();
        },
        queryGasPrice() {
            const mdex_api  =   "defibox/dgg/index/gasprice";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result = res.data;
                    if (result.code == 200) {
                        this.mdex_panel.defibox =   JSON.parse(JSON.stringify(result.data));
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        async panelData () {
            this.mdex_panel.block_number    =   await HecoWeb3.eth.getBlockNumber();

            // 回购合约USDT余额
            const erc20_sol     =   erc20Contract(HecoWeb3, "0xa71edc38d189767582c38a3145b5873052c3e47a");
            const repurch_usdt  =   await erc20_sol.contract.methods.balanceOf('0x46900c0c18ace98baab81561b9906dc93287910c').call();
            this.boardroom.repurch_usdt =   Number(repurch_usdt / 1e18);
        },
        async queryPrice(){
            const pool_pair	=	{ 
                tokenA  :   '0x25D2e80cB6B86881Fd7e07dd263Fb79f4AbE033c', 
                tokenB  :   '0xa71edc38d189767582c38a3145b5873052c3e47a', 
                pair    :   '0x615E6285c5944540fd8bd921c9c8c56739Fd1E13' 
            }

            // 获取MDX三日均价
            const destory_sol   =   destoryOracleContract(HecoWeb3);
            let repurch_price   =   await destory_sol.contract.methods.consult(pool_pair.tokenA, '1000000000000000000', pool_pair.tokenB).call();
            this.boardroom.repurch_price    =   Number(repurch_price / 1e18);

            // 当前MDX现价
            const oracle_sol    =   oracleContract(HecoWeb3);
            let curr_price  =   await oracle_sol.contract.methods.consult(pool_pair.tokenA, '1000000000000000000', pool_pair.tokenB).call();
            this.boardroom.curr_price   =   Number(curr_price / 1e18);
        },
        async queryBalances() {
            const repurch_addrs     =   this.accounts.repurch;
            const repurch_data      =   [];
            for (const repurch of repurch_addrs) {
                const balance   =   await HecoWeb3.eth.getBalance(repurch);
                repurch_data.push({
                    address: repurch,
                    balance: Number(balance / 1e18),
                    addr_type: '回购地址'
                })
            }
            this.repurch_data   =   repurch_data;

            const oracle_addrs     =   this.accounts.oracle;
            const oracle_data      =   [];
            for (const oracle of oracle_addrs) {
                const addr_type =   oracle == '0xa6420fe4f01A53669C7285A536464564fc157034'  ?   '回购预言机地址'   :   '交易对预言机地址';
                const balance   =   await HecoWeb3.eth.getBalance(oracle);
                oracle_data.push({
                    address: oracle,
                    balance: Number(balance / 1e18),
                    addr_type
                })
            }
            this.oracle_data   =   oracle_data;
        },
        async queryRepurchList() {
            const hecoinfo_api  = "https://api.hecoinfo.com/api";
            try {
                this.axios.get(`${hecoinfo_api}`, {
                params: {
                    module: 'account',
                    action: 'txlist',
                    address: '0x46900C0c18ace98bAAB81561B9906Dc93287910C',
                    startblock: 0,
                    endblock: 99999999,
                    page: 1,
                    offset: 10,
                    sort: 'desc'
                }}).then ( async res =>{
                    const result = res.data;
                    if (result.status == 1) {
                        const repurch_data  =   [];
                        const repurch_list  = result['result'];
                        for (const repurch of repurch_list) {
                            const times = timestampToTime(repurch.timeStamp);

                            repurch_data.push({
                                block_number: repurch.blockNumber,
                                hash: repurch.hash,
                                from: repurch.from,
                                to: repurch.to,
                                repurch_time: times
                            })
                        }

                        this.repurch_list   =   repurch_data;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        queryMdexCharts() {
            const mdex_api  =   "https://api.mdex.com/mdex/charts";
            try {
                this.axios.get(`${mdex_api}`).then ( async res =>{
                    const result    =   res.data;
                    if (result.code == 0) {
                        this.mdex_panel.charts    =   result.result;
                    }
                });
            }catch (error) {
                console.log(error);
            }
        },
        useNumberFilter (value, cut) {
            return numberFilter(value, cut);
        },
        showRepurch(index) {
            window.open('https://hecoinfo.com/address/' + this.repurch_data[index]['address'], '_blank').location;
        },
        showOracle(index) {
            window.open('https://hecoinfo.com/address/' + this.oracle_data[index]['address'], '_blank').location;
        },
        repurchInfo(index, stype='hash') {
            if (stype == 'from') {
                window.open('https://hecoinfo.com/address/' + this.repurch_list[index]['from'], '_blank').location;
            } else if (stype == 'to') {
                window.open('https://hecoinfo.com/address/' + this.repurch_list[index]['to'], '_blank').location;
            } else {
                window.open('https://hecoinfo.com/tx/' + this.repurch_list[index]['hash'], '_blank').location;
            }
        }
    }
}
</script>
<style lang="stylus">
.claim-area {
    width : 1280px;
    margin : 0 auto;

    .area-input {
        margin-top : 20px;
        padding-bottom: 30px;
    }

    .gas_area {
        padding-top: 15px;
    }

    .txt_gas {
        font-weight : 500;
        color: var(--main-l1);
        font-family: PingFang SC;
    }

    .txt-red {
        color:#5cb85c
    }
}
</style>